
.board-container {
  position: relative;
  padding-bottom: 33.33%; 
}

@media screen and (max-width:767px)
{
  .board-container {
    padding-bottom: 100%;
  }
}

.board {
  position: absolute;
  top: 0; bottom: 0; left: 0; right: 0;
  background: #ffffff00;
  display: flex;
  flex-wrap: wrap;
  padding: 20px;
}
