.nav {
  margin-top: 20px;
  margin-bottom: 20px;
}
.nav-list {
  height: 80px;
}

.nav-list li {
  display: flex;
  align-items: center;
  background-color: rgb(192, 129, 58);
  text-align: center;
  font-size: 20px;
  border: none;
}
.nav-list li:hover,
.nav-list li.active {
  background-color: rgba(255, 255, 255, 0.507);
  box-shadow: 1px 1px 1px #797676;
  color: black;
  cursor: pointer
}
